@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=DM+Serif+Display:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-dm-serif-display: "DM Serif Display";
  --font-playfair-display: "Playfair Display";

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-5xs: 8px;
  --font-size-11xl: 30px;
  --font-size-2xs: 11px;
  --font-size-8xs: 5px;
  --font-size-mini: 15px;
  --font-size-4xs: 9px;
  --font-size-3xs: 10px;
  --font-size-9xl: 28px;
  --font-size-16xl: 35px;
  --font-size-7xs: 6px;
  --font-size-xs: 12px;

  /* Colors */
  --color-white: #fff;
  --color-orange: #ffa200;
  --color-gray-100: rgba(0, 0, 0, 0.7);
  --color-gray-200: rgba(255, 255, 255, 0.63);
  --color-gray-300: rgba(0, 0, 0, 0.8);
  --color-black: #000;
  --color-saddlebrown-100: #5f4314;
  --color-saddlebrown-200: #4b350f;
  --color-cornsilk: #fff8dc;
  --color-darkgray: #a79d9d;
}
